.form {
    display: flex;
    justify-content: center;
}

.input {
    width: 50%;
    padding: 10px;
    font-size: 16px;
    border-radius: 4px;
    border: none;
    background-color: var(--color-pink-light);
}

.button {
    background-color: transparent;
    border: none;
    cursor: pointer;
    position: relative;
    left: -20px;
}